import React, {useState, useContext} from 'react';
import Record from './Record';
const data = require('../sampleSources/datasample.json')
import {RecordsContext} from "./contextManagers/RecordsProvider"

const Records = () => {
  const {records} = useContext(RecordsContext);

  return (
    <div>
        <h4>Recent inserts</h4>
        {records.map(records => (
          <Record procedureName={data.procedureName} />
        ))}
    </div>
  );
};

export default Records;
